import React from 'react';
import { Helmet } from 'react-helmet';
import { useLogout, useQueryParams } from 'shared/hooks';

function AuthEmailConfirmation() {
	const { query } = useQueryParams<{ is_registering_new: string }>();
	const isRegisteringNew = !!query.is_registering_new;

	useLogout({ isEmailConfirmation: isRegisteringNew });

	return (
		<div className={'h-full w-full p-8'}>
			<>
				<Helmet>
					<title>KENNEKT | Log out for email confirmation</title>
				</Helmet>
				<h1 className={'text-center text-xl font-bold'}>
					Wait... you will be redirected appropriate page.
				</h1>
			</>
		</div>
	);
}

export default AuthEmailConfirmation;
